.profile__rounded{
  width: 25vw;
  border-radius: 50%;
}

.education__text-center{
  text-align: center;
}

.education__text-left{
  text-align: left;
}

.icon__size{
  font-size: 6em;
}


.icon__size-medium{
  font-size: 5vw;
  margin: 2vw;
}

.inlineClass{ 
  display: inline;
}


.icon__size-small{
  font-size: 3em;
  margin-right: .25em;
}

.icon__white{
  color:white;
}


/*global styles*/
ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a.nostyle:link {
  text-decoration: inherit;
  color: inherit;
  cursor: auto;
}

a.nostyle:visited {
  text-decoration: inherit;
  color: inherit;
  cursor: auto;
}

*{
  scroll-behavior: smooth;
  font-family: 'Open Sans', sans-serif;
}


