.header__background{
  background: linear-gradient(to left top, #28a5f5, #1e87f0) 0 0 no-repeat;
}


.header__button{
  background: lightsalmon;
}

.header__text-sub{
  text-shadow: 2px 3px 5px rgba(0,0,0,0.5);
  color: ivory;
}

.header__text-main{
  text-shadow: 2px 3px 5px rgba(0,0,0,0.5);
  font-size: 8vh;
  color: ivory;
}

.object-div{
  height: 100%;
  width:100%;
}

